import React, { useEffect, useState } from "react";
import Layout from '../layout/Layout';
import { Typography, Paper, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Helmet } from 'react-helmet';
import { AuthenticatedTemplate, UnauthenticatedTemplate } from "@azure/msal-react";

import { useMsal, useAccount } from "@azure/msal-react";
import { InteractionStatus} from "@azure/msal-browser";
import { loginRequest } from "../configs/authConfig";
import { callMsGraph } from "../services/GraphService";
import {BlogGetById} from "../services/BlogService";

const useStyles = makeStyles((theme) => ({
    root: {
        margin: 20,
        padding: 20
    },
    sectionTitle: {
        paddingBottom: 10
    }
}));


export default function Index() {
    const classes = useStyles();
    const { instance, accounts, inProgress } = useMsal();
    const account = useAccount(accounts[0] || {});

    if (account && inProgress === InteractionStatus.None) {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: account
        }).then((response) => {
            console.log(account.name);
            callMsGraph(response.accessToken).then(response => console.log(response));
        });
    }  
    
    BlogGetById("20210312064310").then(response => console.log(response));

    return (
        <Layout>
            <Paper elevation={3} className={classes.root} >
                <Typography variant="h6" className={classes.sectionTitle}>
                    <AuthenticatedTemplate>
                    <Typography variant="h6">
                        <center>Logged-In.</center>
                        </Typography>
                    </AuthenticatedTemplate>

                    <UnauthenticatedTemplate>
                        <Typography variant="h6">
                        <center>Please sign-in to see your profile information.</center>
                        </Typography>
                    </UnauthenticatedTemplate>
                </Typography>              
            </Paper>
            <Helmet>
                <title>Bipul Raman's Web</title>
            </Helmet>
        </Layout>
    );
}
